:root {
  --primary-color: #1f2937;
  --primary-color-hover: #334155;
  --error-color: #dc2626;
  --error-color-hover: #b91c1c;
  --success-color: #059669;
  --success-color-hover: #047857;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #374151;
  color: #1a202c;
  line-height: 1.6;
}


#registration-container,
#login-container{
  max-width: 350px;
  margin: 100px auto;
  padding: 30px;
  background: url(https://media4.giphy.com/media/BHNfhgU63qrks/giphy.gif) center center no-repeat;
  background-size: cover;
  border-radius: 12px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4);
  margin-bottom: 40px;
 
}
#registration-container,
#login-container h1{
  color: #fff;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="password"] {
  width: 100%;
  height: 40px;
  margin-top: 20px;
  background: rgba(255,255,255,.5);
  border: 1px solid rgba(255,255,255,.1);
  padding: 0 15px;
  color: #FFF;
  border-radius: 5px;
  font-size: 14px;
}

input[type="text"],
input[type="password"]:focus{
  border: 1px solid rgba(255,255,255,.8);
  outline: none;
}
::-webkit-input-placeholder {
    color: #DDD;
}

li input[type="text"] {
  margin: auto 10px;
  max-width: 50%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  color:#1a202c;
}

#task-input{
  color: #1a202c;
}


button[type="submit"] {
  background-color: var(--primary-color);
  color: #fff;
  border: 0;
  padding: 10px;
  font-size: 18px;
  border-radius: 4px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: var(--primary-color-hover);
}

.alert {
  padding: 10px;
  margin-bottom: 20px;
  display: none;
}

.alert.error {
  background-color: var(--error-color);
  color: #ffffff;
}

.alert.success {
  background-color: var(--success-color);
  color: #ffffff;
}

h1 {
  margin-bottom: 20px;
}

#logged-in-section {
  max-width: 700px;
  margin: 0 auto;
  padding: 30px;
  background: rgba(100, 222, 150, 0.1);
  border-radius: 4px;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4);
  margin-bottom: 40px;
}

ul {
  list-style: none;
 }

li {
  background-color: #edf2f7;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  
}



button.edit-button,
button.delete-button {
  background-color: transparent;
  border: 0;
  font-size: 14px;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 4px;
}

h1#todo-title {
  text-align: center;
}

#logout-button {
  background-color: var(--error-color);
  color: #fff;
  border: 0;
  padding: 10px;
  font-size: 18px;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 20px;
  display: inline-block;
}

#logout-button:hover {
  background-color: var(--error-color-hover);
}

#card-container {
  display: grid;
  place-items: center;
  perspective: 1000px;
}

.card {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.6s;
}

.card.flipped {
  transform: rotateY(180deg);
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.front {
  background-color: #fff;
  
}

.back {
  background-color: #fff;
  transform: rotateY(180deg);
}

.flip-btn {
  display: inline-block;
  padding: 10px;
  background-color: var(--primary-color);
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

.flip-btn:hover {
  background-color: var(--primary-color-hover);
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 2s ease-in-out;
}

button.save-button,
button.edit-button,
button.delete-button {
  background-color: #0077cc;
  color: #fff;
  border: 0;
  font-size: 14px;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 4px;
  margin-left: 5px;
}

button.save-button {
  background-color: #4CAF50;
}

button.save-button:hover {
  background-color: #43A047;
}

button.edit-button:hover {
  background-color: #005fa3;
}

button.delete-button {
  background-color: #f44336;
}

button.delete-button:hover {
  background-color: #d32f2f;
}


.like-icon {
  font-size: 16px;
  cursor: pointer;
  color: #0077cc;
  margin-right: 10px;
}

li.favorite .like-icon {
  color: #e74c3c;
}

.animated-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(270deg, #84f2ff, #ffbae8, #84f2ff);
  background-size: 400% 400%;
  animation: gradientAnimation 6s ease-in-out infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

